<template>
  <div class="result animated fadeIn">
    <div class="pic-template" id="canvas">
      <img class="bg" src="../assets/测试结果页/base@3x.png" />

      <img class="title-img" src="../assets/测试结果页/矩形@3x.png"/>
      <div class="nick-name">{{ nickName }}</div>

      <div class="south">
        <img v-for="(li, index) in south" :key="index" src="../assets/测试结果页/矩形@3x(9).png" />
      </div>
      <div class="number">{{ sgene }}%</div>

      <div class="nouth">
        <img v-for="(li, index) in nouth" :key="index" src="../assets/测试结果页/矩形@3x(2).png" />
      </div>
      <div class="number numberN">{{ ngene }}%</div>

      <div class="title">
        {{ title }}
      </div>
      <div class="desc">
        {{ desc }}
      </div>
      <img class="bar" src="../assets/测试结果页/gradient@3x.png" />
      <img class="qrBlock" src="../assets/QRB.png" />
      <img class="logo" src="../assets/测试结果页/logo@3x.png" />
    </div>

    <div class="btn" onclick="window.location.href='https://www.heartide.com/download/smallsleep'">加入南北PK大战</div>

    <img class="outer-img" :src="imgSrc" />
  </div>
</template>

<script>
import html2canvas from 'html2canvas'

export default {
  data () {
    return {
      imgSrc: '',
      nickName: localStorage.getItem('name') || '洪洞人',
      south: Array(3),
      nouth: Array(6),
      sgene: sessionStorage.getItem('sgene'),
      ngene: sessionStorage.getItem('ngene'),
      title: `基因属性=${sessionStorage.getItem('sgene')}%南方基因+${sessionStorage.getItem('ngene')}%北方基因`,
      desc: sessionStorage.getItem('desc')
    }
  },
  methods: {
    parseToPic () {
      const dom = document.querySelector('#canvas')
      // const container = document.querySelector('.outer-img')
      html2canvas(dom, {
        useCORS: true,
        onclone: (document) => {
          document.querySelector('#canvas').style.visibility = 'initial'
          // const width = document.querySelector('#canvas').style.width
          // document.querySelector('#canvas').style.width = width * 1.1
        }
      }).then((canvas) => {
        const img = new Image()
        img.src = canvas.toDataURL()
        img.style.width = '100%'

        img.style.position = 'absolute'
        img.style.left = '50%'
        img.style.top = '50%'
        img.style.transform = 'translateX(-50%) translateY(-50%)'

        this.imgSrc = img.src
        // container.appendChild(img)
        // dom.innerHTML = ''
        // dom.appendChild(img)
        // dom.style.visibility = 'initial'
        // dom.classList.add('change-canvas-border-radius')
      })
    }
  },
  mounted () {
    let s = parseInt(sessionStorage.getItem('sgene').padStart(2, '1').split('')[0])
    let n = parseInt(sessionStorage.getItem('ngene').padStart(2, '1').split('')[0])
    if (sessionStorage.getItem('sgene') === '100') {
      s = 10
      n = 0
    }
    if (sessionStorage.getItem('ngene') === '100') {
      n = 10
      s = 0
    }
    if (parseInt(sessionStorage.getItem('sgene')) >= 10 && parseInt(sessionStorage.getItem('sgene')) <= 90 && parseInt(sessionStorage.getItem('sgene').split('')[sessionStorage.getItem('sgene').split('').length - 1]) > 5 && sessionStorage.getItem('sgene') !== '100' && sessionStorage.getItem('ngene') !== '100') {
      s += 1
    }
    if (parseInt(sessionStorage.getItem('ngene')) >= 10 && parseInt(sessionStorage.getItem('ngene')) <= 90 && parseInt(sessionStorage.getItem('ngene').split('')[sessionStorage.getItem('ngene').split('').length - 1]) > 5 && sessionStorage.getItem('sgene') !== '100' && sessionStorage.getItem('ngene') !== '100') {
      n += 1
    }
    this.south = Array(s)
    this.nouth = Array(n)
    setTimeout(() => {
      this.parseToPic()
    }, 500)
  },
  name: 'Result'
}
</script>

