import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import WJH from '@/js/wjhJS.js'
import animated from 'animate.css'
import Share from '@/js/shareAndGetName'
import Tool from './js/tool.js'

Vue.prototype.WJH = WJH
Vue.config.productionTip = false
require('@/js/setSize')
Vue.use(animated)

new Vue({
  router,
  store,
  data () {
    return {
      url: process.env.NODE_ENV === 'production' ? 'https://api.debug.psy-1.com' : 'https://api.debug.psy-1.com',
      share: ''
    }
  },
  methods: {
    shareM (title = '小睡眠', desc = 'asd', picUrl) {
      // eslint-disable-next-line
      const share = new Share({ pic: picUrl, url: window.location.href.split('#')[0], title: title, desc: desc })
      this.share = share
      share.appShare()
      share.rawWeiXinShare(this.url)
    },
    test () {
      if (Tool.is_cosleep()) {
        this.share.appGetName()
      }
      if (/micromessenger/.test(navigator.userAgent.toLowerCase())) {
        this.share.weiXinInit(this.url)
        this.share.weatherCode()
        this.share.weiXinGetName(this.url)
      }
    }
  },
  mounted () {
    this.shareM('南北粽子PK战', '测一测你的南北基因属性！', 'https://res.psy-1.com/Frl3KjfZ1pXxiaY3vDbV04V9nNdy')
    this.test() // 获取名称
  },
  render: h => h(App)
}).$mount('#app')
