<template>
  <div class="input">
    <div class="outer">
      <input placeholder="输入名字" v-model="asd" maxlength="8" class="myip" />
      <div class="abuttob" @click="qd">确定</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Input',
  data () {
    return {
      asd: ''
    }
  },
  methods: {
    qd () {
      if (!this.asd) {
        return
      }
      localStorage.setItem('name', this.asd)
      this.$parent.inputShow = false
      this.$router.push('./subject')
    }
  }
}
</script>

